<template>
  <app-content-wrapper :pageTitle="pageTitle">
    <ui-loading :activeModel="!previewPostLoaded" :fullPage="true"></ui-loading>
    <ui-loading :activeModel="!tmpPostListLoaded" :fullPage="true"></ui-loading>

    <template v-if="!dataLoaded">
      <ui-skeletor :height="'1.3rem'"></ui-skeletor>
      <ui-skeletor :height="'1.3rem'"></ui-skeletor>
      <ui-skeletor :height="'1.3rem'"></ui-skeletor>
    </template>

    <div class="write__wrapper" v-else>
      <ui-form :name="'savePostForm'"
               :ref="'savePostForm'"
               :class="'write__frm'"
               :preventEnter="true"
               @onsubmit="onSubmit"
      >
        <div class="write__save-wrapper">
          <ui-button :type="'button'"
                     :color="'primary'"
                     :class="'write__btn--load'"
                     @click="listTmpPost">
          </ui-button>
    
          <ul class="write__save-list" v-if="0 < tmpPostList.length">
            <li v-for="(tmpPost,i) in tmpPostList" :key="i">
              <ui-icon-button :type="'button'"
                              :icon="'xi-close-min'"
                              :text="''"
                              :class="'write__save-list__delete'"
                              @click="removeTmpPost(tmpPost.id)">
              </ui-icon-button>

              <a href="javascript:;" class="link" @click.prevent="applyTmpPost(tmpPost)">{{ tmpPost.title }}</a>
              <span class="write__save-list__date">{{ tmpPost.regDate }}</span>
            </li>
          </ul>
        </div>

        <ui-hidden-field :name="'id'" :id="'savePostId'" :value="post?.id"></ui-hidden-field>
    
        <ui-write-table :name="'  '">
          <tr>
            <th scope="row">
              <label for="savePostTitle"></label>
            </th>
            <td>
              <div class="write__title">
                <ui-select :name="'categoryId'"
                           :id="'savePostCategory'"
                           :class="'write__select-category'"
                           :title="' '"
                           :defaultValue="' '"
                           :rules="'required'"
                           :data="categoryList"
                           v-model="categoryId">
                </ui-select>
  
                <ui-text-field :type="'text'"
                               :name="'title'"
                               :id="'savePostTitle'"
                               :rules="'required'"
                               :block="true"
                               :value="title"
                               v-model="title">
                </ui-text-field>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostCont"></label>
            </th>
            <td>
              <ui-textarea :name="'cont'"
                           :id="'savePostCont'"
                           :cols="'80'"
                           :rows="'18'"
                           :rules="'required'"
                           :hidden="true"
                           v-model="cont">
              </ui-textarea>

              <md-editor :language="'en-US'"
                         :preview="false"
                         v-model="cont">
              </md-editor>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostOgDesc"> </label>

              <ui-icon-button :type="'button'"
                              :icon="'xi-refresh'"
                              :text="'  '"
                              :class="'write__og-desc-refresh'"
                              @click="changeOgDesc">
              </ui-icon-button>
            </th>
            <td>
              <ui-text-field :type="'text'"
                             :name="'ogDesc'"
                             :id="'savePostOgDesc'"
                             :class="'write__og-desc'"
                             :placeholder="'50 ( ,  )'"
                             :block="true"
                             :value="ogDesc"
                             v-model="ogDesc">
              </ui-text-field>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostOgImgFile"> </label>
            </th>
            <td>
              <ui-hidden-field :name="'ogImg'" :id="'savePostOgImg'" :value="post?.ogImg"></ui-hidden-field>
              <ui-hidden-field :name="'ogImgUrl'" :id="'savePostOgImgUrl'" :value="post?.ogImgUrl"></ui-hidden-field>
              <ui-hidden-field :name="'ogImgSize'" :id="'savePostOgImgSize'" :value="post?.ogImgSize"></ui-hidden-field>

              <ui-file-field :name="'ogImgFile'"
                             :id="'savePostOgImgFile'"
                             :accept="'image/*'"
                             :gap="10"
                             @onchange="onChangeOgImg">
                
                <ui-file-button :type="'button'"
                                :color="'secondary'"
                                @clickFile="onClickFile">Cloudinary
                </ui-file-button>
              </ui-file-field>

              <ui-file-info :imgName="ogImg"
                            :imgSize="ogImgSize"
                            v-if="post?.ogImg"
              >
                <ui-checkbox :name="'delOgImg'"
                             :id="'savePostDelOgImg'"
                             :class="'ml--10'"
                             :label="''"
                             :values="'Y,N'">
                </ui-checkbox>
              </ui-file-info>
            </td>
          </tr>
          <tr v-if="'D01002' === $route.meta.pageType">
            <th scope="row">
              <label for="savePostAddTag"></label>
            </th>
            <td>
              <ui-hidden-field :name="'saveTagList'" :id="'saveTagList'"></ui-hidden-field>
            
              <div class="write__tag__wrapper">
                <ui-text-field :type="'text'"
                               :name="'addTag'"
                               :id="'savePostAddTag'"
                               :class="'write__tag'"
                               :placeholder="'  ( , 5  )'"
                               :block="true"
                               v-model="tagStr">
                </ui-text-field>

                <ui-select :name="'tag'"
                           :id="'savePostTag'"
                           :class="'write__select-tag'"
                           :title="' '"
                           :defaultValue="' '"
                           :data="tagList"
                           @onchange="onChangeTag">
                </ui-select>
              </div>
            </td>
          </tr>
          <tr v-if="'D01002' === $route.meta.pageType">
            <th scope="row"></th>
            <td>
              <ui-radio :id="'savePostSecretN'"
                        :name="'secretYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'N'"
                        v-model="secretYn">
              </ui-radio>
          
              <ui-radio :id="'savePostSecretY'"
                        :name="'secretYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="secretYn">
              </ui-radio>
            </td>
          </tr>
          <tr v-if="'D01002' === $route.meta.pageType">
            <th scope="row"></th>
            <td>
              <ui-radio :id="'savePostPinN'"
                        :name="'pinYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'N'"
                        v-model="pinYn">
              </ui-radio>

              <ui-radio :id="'savePostPinY'"
                        :name="'pinYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="pinYn">
              </ui-radio>
            </td>
          </tr>
          <tr>
            <th scope="row"></th>
            <td>
              <ui-radio :id="'savePostTmpN'"
                        :name="'tmpYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'N'"
                        v-model="tmpYn">
              </ui-radio>

              <ui-radio :id="'savePostTmpY'"
                        :name="'tmpYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="tmpYn">
              </ui-radio>
            </td>
          </tr>

          <template v-slot:btn>
            <ui-button :type="'button'"
                       :color="'success'"
                       :class="'write__btn'"
                       @click="previewPost">
            </ui-button>

            <ui-button :type="'reset'"
                       :color="'secondary'"
                       :class="'write__btn'">
            </ui-button>

            <ui-button :type="'submit'"
                       :color="'primary'"
                       :class="'write__btn'">
            </ui-button>
          </template>
        </ui-write-table>
      </ui-form>
    </div>
  </app-content-wrapper>
</template>

<script>
import { arrayHasDuplicateValue, getFormValues, isBlank, isNotEmpty, messageUtil } from '@/utils';
import { breadcrumbService } from '@/services/breadcrumb/breadcrumbService';
import AppSavePostTag from '@/components/views/post/AppSavePostTag.vue';
import AppPreviewPostModal from '@/components/views/post/AppPreviewPostModal.vue';

import MdEditor from 'md-editor-v3';
import 'md-editor-v3/lib/style.css';

export default {
  name: 'app-add-post',
  components: {
    AppSavePostTag,
    MdEditor,
  },
  data() {
    return {
      /**   */
      pageTitle: '',
      /**  */
      post: null,
      /**   */
      title: '',
      /**   */
      cont: '',
      /**   */
      ogDesc: '',
      /**   */
      ogImg: '',
      /**    */
      ogImgSize: '',
      /**    */
      ogImgFileList: [],
      /**    */
      categoryList: [],
      /**   ID */
      categoryId: '',
      /**    */
      tagList: [],
      /**    */
      secretYn: 'N',
      /**    */
      pinYn: 'N',
      /**    */
      tmpYn: 'N',
      /**  /     */
      tagStr: '',
      /**  /     */
      saveTagList: [],
      /**    */
      tmpPostList: [],
      /**     */
      dataLoaded: false,
      /**      */
      previewPostLoaded: false,
      /**       */
      tmpPostListLoaded: false,
    }
  },
  /**         */
  async beforeRouteLeave(to, from) {
    if ('/a/goto' === to.path || '/' === to.path) return true;

    const confirm = await messageUtil.confirmWarning('  ?', '     .');
    if (!confirm) return false;
  },
  created() {
    this.pageTitle = !this.isUpdatePage ? ' ' : ' ';
    
    //   
    breadcrumbService.setPageTitle(this.pageTitle);

    this.init();
  },
  computed: {
    /**     */
    isUpdatePage: {
      get() {
        return isNotEmpty(this.$route.params.id);
      },
      set(v) {}
    }
  },
  methods: {
    /**   */
    async init() {
      this.previewPostLoaded = true;
      this.tmpPostListLoaded = true;

      //    ,     
      if (this.isUpdatePage) {
        await Promise.all([
          this.listCategory(),
          this.listTag(),
          this.getPost(this.$route.params.id),
        ]);
      } else {
        this.dataLoaded = true;
        
        await Promise.all([
          this.listCategory(),
          this.listTag(),
        ]);
      }

      this.dataLoading();
    },
    /**   */
    async onSubmit(values) {
      const msg = ['?', ''];

      //   
      if ('Y' === values.tmpYn) {
        msg[0] = '' + msg[0];
        msg[1] = '' + msg[1];
      }

      const isValid = this.validationCheck();
      if (!isValid) return;

      const confirm = await messageUtil.confirmSuccess(msg[0]);
      if (!confirm) return;

      //   
      this.setTagArr(values);
      
      const headers = { 'Content-Type': 'multipart/form-data' };

      if (this.isUpdatePage) {
        await this.$http.put('/post', values, { headers });
      } else {
        await this.$http.post('/post', values, { headers });
      }

      messageUtil.toastSuccess(msg[1]);

      this.$store.dispatch('Post/FETCH_MAIN_POSTLIST', {});
      this.$store.dispatch('Layout/FETCH_SIDEBAR', {});

      //     
      if ('N' === values.tmpYn) {
        this.$router.push('/');
      }
    },
    /**   */
    getPost(id) {
      return this.$http.get(`/post/${id}`)
      .then(res => {
        this.post = { ...res.data };
        this.setData(this.post);
      });
    },
    /**    */
    setData(post) {
      this.$refs['savePostForm']?.setFieldValue('id', post.id);
      this.$refs['savePostForm']?.setFieldValue('ogImg', post.ogImg);
      this.$refs['savePostForm']?.setFieldValue('ogImgUrl', post.ogImgUrl);
      this.$refs['savePostForm']?.setFieldValue('ogImgSize', post.ogImgSize);

      this.title = post.title;
      this.cont = post.rawText;
      this.secretYn = post.secretYn;
      this.pinYn = post.pinYn;
      this.tmpYn = post.tmpYn;
      this.categoryId = post.postCategory[0].categoryId;
      this.ogDesc = post.ogDesc;
      this.ogImg = post.ogImg;
      this.ogImgSize = post.ogImgSize;
      this.tagStr = post.postTag.map(d => d.tag.nm).join(', ');
    },
    /**      */
    async changeOgDesc() {
      const title = this.$refs['savePostForm'].getFieldValue('title');

      if (isBlank(title)) {
        messageUtil.toastWarning(' .');
        return;
      }

      const confirm = await messageUtil.confirmSuccess('   ?');
      if (!confirm) return;

      this.$refs['savePostForm'].setFieldValue('ogDesc', title);
    },
    /** cloudinary    */
    onClickFile(file) {
      this.$refs['savePostForm'].setFieldValue('ogImg', file.public_id + '.' + file.format);
      this.$refs['savePostForm'].setFieldValue('ogImgUrl', file.url);
      this.$refs['savePostForm'].setFieldValue('ogImgSize', file.bytes);
    },
    /**    file input    */
    onChangeOgImg(values) {
      if (isNotEmpty(values)) {
        this.ogImg = values.name;
        this.ogImgSize = values.size;
      }
    },
    /**    */
    listCategory() {
      return this.$http.get('/category')
      .then(res => {
        res.data.map(d => {
          this.categoryList.push({
            value: d.id,
            text: d.nm,
          });
        });
      });
    },
    /**    */
    listTag() {
      return this.$http.get('/tag')
      .then(res => {
        res.data.map(d => {
          this.tagList.push({
            value: d.id,
            text: d.nm,
          });
        });
      });
    },
    /**    */
    onChangeTag(value) {
      const foundTag = this.tagList.find(d => d.value == value).text;

      if (isBlank(this.tagStr)) {
        this.tagStr = foundTag;
      } else {
        this.tagStr += `, ${foundTag}`;
      }
    },
    /**    */
    setTagArr(formValues) {
      if (isBlank(this.tagStr)) {
        return;
      }

      this.saveTagList = [];

      const tagArr = this.tagStr.split(',');

      for (let i = 0; i < tagArr.length; i++) {
        const foundTag = this.tagList.find(d => d.text === tagArr[i].trim());

        this.saveTagList.push({
          // DB       id  .   UPDATE,  INSERT  
          ...(isNotEmpty(foundTag) && { id: foundTag.value }),
          nm: tagArr[i].trim(),
        });
      }

      formValues['saveTagList'] = JSON.stringify(this.saveTagList);
    },
    /**   */
    previewPost() {
      const body = getFormValues(this.$refs['savePostForm'].$el);
      
      this.previewPostLoaded = false;

      return this.$http.post('/post/preview', body)
      .then(res => {
        this.previewPostLoaded = true;

        this.$modal.show({
          component: AppPreviewPostModal,
          bind: {
            post: res.data,
          },
        });
      });
    },
    /**     */
    listTmpPost() {
      if (0 < this.tmpPostList.length) {
        this.tmpPostList = [];
        this.tmpPostListLoaded = true;
        return;
      }

      this.tmpPostListLoaded = false;

      const listPostDto = { tmpYn: 'Y' };

      return this.$http.get('/post', { params: listPostDto })
      .then(res => {
        res.data[0].map(d => {
          d.regDate = this.$moment(d.regDate).format('YYYY-MM-DD HH:mm:ss');
        });

        this.tmpPostList = [...res.data[0]];
        this.tmpPostListLoaded = true;
      });
    },
    /**    */
    async applyTmpPost(tmpPost) {
      const confirm = await messageUtil.confirmSuccess('?');
      if (!confirm) return;

      this.post = { ...tmpPost };
      this.setData(this.post);
    },
    /**    */
    async removeTmpPost(postId) {
      const confirm = await messageUtil.confirmSuccess('?');
      if (!confirm) return;

      return this.$http.delete(`/post/${postId}`)
      .then(res => {
        messageUtil.toastSuccess('.');
        this.tmpPostList = this.tmpPostList.filter(d => d.id !== postId);
      });
    },
    /**   */
    validationCheck() {
      const tagArr = this.tagStr.split(',');

      if (arrayHasDuplicateValue(tagArr)) {
        messageUtil.toastWarning('  .');
        return false;
      }

      if (5 < tagArr.length) {
        messageUtil.toastWarning(' 5   .');
        return false;
      }

      return true;
    },
    /**   */
    dataLoading() {
      if (isNotEmpty(this.post)) {
        this.dataLoaded = true;
      }
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/views/write.scss';
</style>